<template>
    <div class="chart">
        <div ref="sunchart" class="echarts" style="width:800px;height:500px"></div>
    </div>
</template>

<script>
    import * as echarts from "echarts/core";
    import { TitleComponent, LegendComponent, TooltipComponent } from "echarts/components";
    import { PieChart } from "echarts/charts";
    import { SVGRenderer } from "echarts/renderers";
    export default {
        props: ["Year", "Medal"],
        async mounted() {
            echarts.use([TitleComponent, LegendComponent, TooltipComponent, PieChart, SVGRenderer]);
            this.dataset = await fetch("./每年奖牌趋势.json").then((res) => res.json());

            this.myChart = echarts.init(this.$refs.sunchart);
            this.reDraw();
        },
        data() {
            return {
                dataset: {},
                myChart: {},
                rendered: false,
                option: {
                    legend: {
                        top: "5%",
                        left: "center",
                    },
                    tooltip: {
                        trigger: "item",
                    },
                    title: {
                        text: "",
                        top: "center",
                        left: "center",
                    },

                    series: [],
                },
            };
        },
        methods: {
            sort(data) {
                let values = Object.values(data);
                const avg = Math.floor(values.reduce((a, b) => a + b, 0) / values.length) * 2;
                return Object.entries(data).reduce(
                    (arr, [key, value]) => {
                        if (value > avg) {
                            arr.push({ name: key + "-" + value, value });
                        } else {
                            arr[0].value += value;
                        }

                        return arr;
                    },
                    [{ name: "其他国家", value: 0 }]
                );
            },
            reDraw() {
                console.log(this.Medal);
                const target = this.dataset[this.Year][this.Medal];
                let Medal;
                switch (this.Medal) {
                    case "Gold":
                        Medal = "金牌";
                        break;
                    case "Silver":
                        Medal = "银牌";
                        break;
                    case "Bronze":
                        Medal = "铜牌";
                        break;
                }
                this.option.title.text = this.Year + Medal;
                const that = this;
                this.option.series = [target].map((data, index) => ({
                    type: "pie",
                    radius: ["40%", "70%"],

                    itemStyle: {
                        borderRadius: 8,
                    },
                    emphasis: {
                        label: {
                            show: true,
                            fontSize: "20",
                            fontWeight: "bold",
                        },
                    },

                    data: that.sort(data),
                }));
                this.myChart.setOption(this.option);
                console.log(this.option.series);
            },
        },
    };
</script>
<style lang="less" scoped>
    svg {
        text {
            transition: 1s;
        }
    }
</style>
